import React, { useState } from 'react';

export const MobileNav = () => {
  const [isMobileNavActive, setMobileNavActive] = useState(false);

  const mobileNavShow = document.querySelector('.mobile-nav-show');
  const mobileNavHide = document.querySelector('.mobile-nav-hide');

  document.querySelectorAll('.mobile-nav-toggle').forEach((el) => {
    el.addEventListener('click', function (event) {
      event.preventDefault();
      mobileNavToogle(event);
    });
  });

  function mobileNavToogle(e) {
    if (!isMobileNavActive) {
      document.getElementById('root').classList.add('mobile-nav-active');
      mobileNavShow.classList.add('d-none');
      mobileNavHide.classList.remove('d-none');
      setMobileNavActive(true);
    } else {
      document.getElementById('root').classList.remove('mobile-nav-active');
      mobileNavShow.classList.remove('d-none');
      mobileNavHide.classList.add('d-none');
      setMobileNavActive(false);
    }
  }
  document.querySelectorAll('#navbar a').forEach(navbarlink => {

    if (!navbarlink.hash) return;

    let section = document.querySelector(navbarlink.hash);
    if (!section) return;

    navbarlink.addEventListener('click', () => {
      if (document.querySelector('.mobile-nav-active')) {
        mobileNavToogle();
      }
    });

  });
 

 
  return (
    <>
      <i className="mobile-nav-toggle mobile-nav-show bi bi-list"></i>
      <i className="mobile-nav-toggle mobile-nav-hide d-none bi bi-x"></i>
    </>
  );
};
